import { Fragment, useState } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill,  RiErrorWarningLine} from "react-icons/ri";
import { Button, Modal, Row, Col, List, Popconfirm, Form, Input, Image } from "antd";
import noImage from "../../../../assets/images/noimg.png";
import {
  isRepairAmountViewModelVisible,
  repairAmountApproveStatus
} from "../../../../store/Masters/RepairAmount/repairAmountSlice";


const repairAmoutnView = (props) => {
  const [remarks, setRemarks] = useState({});
  const [transferstatus, setTransferstatus] = useState(0);

  const [form] = Form.useForm();

  const handleResetInputValues = () => {
    form.resetFields();
    props.isRepairAmountViewModelVisible(false);
  };

  

  function repairAmountInApprove() {
    props.repairAmountApproveStatus({
      repairAmountObj: {
        rejectdescription: remarks.target?.value,
        repairapprovalstatus: transferstatus,
      },
      id: props.rowDataView.id,
    });
    form.resetFields();
  }

  const repairAmountList = [
    {
      title: "CODE",
      description:
        props.rowDataView.code == null
          ? "none"
          : props.rowDataView.code,
    },

    {
      title: "COUNTER",
      description:
        props.rowDataView.counter?.name == null
          ? "none"
          : props.rowDataView.counter?.name,
    },

    {
      title: "PURCHASE",
      description:
        props.rowDataView.purchase?.code == null
          ? "none"
          : props.rowDataView.purchase?.code,
    },

    {
      title: "VEHICLE NUMBER",
      description:
        props.rowDataView.purchase?.vehicle?.vehicleno == null
          ? "none"
          : props.rowDataView.purchase?.vehicle?.vehicleno,
    },

    {
      title: "AMOUNT",
      description:
        props.rowDataView.amount == null
          ? "none"
          : props.rowDataView.amount,
    },

    {
      title: "DESCRIPTION",
      description:
        props.rowDataView.description == null
          ? "none"
          : props.rowDataView.description,
    },

    {
      title: "REPAIR TYPE",
      description:
        props.rowDataView.repair?.name == null
          ? "none"
          : props.rowDataView.repair?.name,
    },
    
  ];


  const repairTypeImages = [
    {
      title: "RECEIPT IMAGE",
      imgFile:
        props.rowDataView.attachment != null
          ? props.rowDataView.attachment?.file
          : noImage,
      ImgFileThumbnail:
        props.rowDataView.attachment != null
          ? props.rowDataView.attachment?.file_thumbnail
          : noImage,
      createdon: props.rowDataView.attachment?.createdon,
      alt: "RECEIPT IMAGE",
    },
  ]

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Popconfirm
          placement="topRight"
          title="Are you sure to Approve this Record?"
          onConfirm={() => {
            form.submit();
            setTransferstatus(2);
          }}
          okText="Yes"
          cancelText="No"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
          <Button block className="hp-px-12 hp-accept-btn hp-font-weight-400">
            Approve
          </Button>
        </Popconfirm>
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Popconfirm
          placement="topRight"
          title="Are you sure to Reject this Record?"
          onConfirm={() => {
            form.submit();
            setTransferstatus(3);
          }}
          okText="Yes"
          cancelText="No"
          className="hp-ml-8"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
          <Button block className="hp-px-12 hp-reject-btn hp-font-weight-400">
            Reject
          </Button>
        </Popconfirm>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">Repair Amount View</h5>
  );
  // useEffect(() => {
  //   form.setFieldsValue({
  //     description: remarks.target?.value,
  //   });
  // }, [remarks]);

  return (
    <Modal
      visible={props.repairViewModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isRepairAmountViewModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <>
          {props.rowDataView.repairapprovalstatus == 1 && (
            <>
              {props.userAccessList?.indexOf(
                "StockTransfer.approve_repairamount"
              ) !== -1 && (
                <Row justify="center">
                  <Col span={12} className="hp-pl-4">
                    <FooterAddOrUpdate />
                  </Col>

                  <Col span={12} className="hp-pr-4">
                    <FooterCancel />
                  </Col>
                </Row>
              )}
            </>
          )}
        </>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Row>
        <Col span={24} md={24}>
          <List
            grid={{
              gutter: 16,
              xs: 1,
              sm: 2,
              md: 3,
              lg: 3,
              xl: 3,
              xxl: 3,
            }}
            dataSource={repairAmountList}
            renderItem={(item) => (
              <List.Item>
                <List.Item.Meta
                  title={<span>{item.title}</span>}
                  description={<span>{item.description}</span>}
                />
              </List.Item>
            )}
          />

          {props.rowDataView.repairapprovalstatus == 1 && (
            <>
              <Form
                layout="vertical"
                form={form}
                onFinish={() => {
                  repairAmountInApprove();
                }}
              >
                <Form.Item
                  name="rejectdescription"
                  rules={[
                    {
                      required: true,
                      message: "Please Input description",
                    },
                  ]}
                  hasFeedback
                >
                  <Input.TextArea
                    id="rejectdescription"
                    name="rejectdescription"
                    style={{ width: "100%" }}
                    placeholder="Write Remarks..."
                    // value={remarks.target.value}
                    onChange={(e) => setRemarks(e)}
                  />
                </Form.Item>
              </Form>
            </>
          )}
        </Col>

        <Row gutter={[12, 12]}>
          {repairTypeImages.map((repairImg) => {
            return (
              <Col span={24} xs={12} md={24} xl={24}>
                <p className=" hp-m-0 hp-text-color-primary-1 hp-font-weight-600 hp-font-size-12">
                  {repairImg.title}
                </p>
                <Image.PreviewGroup>
                  <Image
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={repairImg.imgFile}
                    alt={repairImg.alt}
                  />
                </Image.PreviewGroup>
              </Col>
            );
          })}
        </Row>
      </Row>
    </Modal>
  );
};

function mapStateToProps({ repairAmount, system, }) {
  return {
    repairViewModel: repairAmount.repairViewModel,
    rowDataView: repairAmount.rowDataView,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  isRepairAmountViewModelVisible,
  repairAmountApproveStatus
})(repairAmoutnView);
