import React, { useEffect, useState } from "react";

import {
  Row,
  Col,
  Input,
  Card,
  Pagination,
  Button,
  Spin,
  Popconfirm,
  Typography,
} from "antd";
import { Table, Space } from "antd";
import { connect } from "react-redux";
import { FilterOutlined, PlusOutlined } from "@ant-design/icons";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import { RiErrorWarningLine } from "react-icons/ri";
import { isMobile } from "react-device-detect";
const { Text, Link } = Typography;
import {
  getRepairAmount,
  getRepairAmountByID,
  repairAmountDelete,
  isModelVisible,
  SetRepairAmountParams,
  isRepairAmountViewModelVisible,
  clearReapirAmountList,
} from "../../../../store/Masters/RepairAmount/repairAmountSlice";
import PageTitle from "../../../../layout/components/content/page-title";
import {
  getPurchaseStock,
  setPurchaseStockParams,
} from "../../../../store/mini/miniSlice";
import AddRepairamount from "./addRepairamount";
import RepairAmountView from "./RepairAmountView";
import moment from "moment";

function RepairAmount(props) {
  useEffect(() => {
    props.getRepairAmount({ page: 1, search: "", page_size: 10 });
  }, []);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      dataIndex: "code",
      filterSearch: true,
      width: 100,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Date",
      render: (row) => <span className={columnClass}>{moment(row.createdon).format('DD-MM-YYYY')}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: 120,
    },
    {
      title: "Counter",
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: 120,
    },
    {
      title: "Vehicle Number",
      render: (row) => (
        <span className={columnClass}>{row.purchase?.vehicle?.vehicleno}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 120,
    },
    {
      title: "Purchase",
      render: (row) => (
        <span className={columnClass}>{row.purchase?.code}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 160,
    },
    {
      title: "Amount",
      render: (row) => <span className={columnClass}>{row.amount}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: 100,
    },
    {
      title: "Description",
      render: (row) => <span className={columnClass}>{row.description}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: 100,
    },
    {
      title: "Repair Type",
      render: (row) => <span className={columnClass}>{row.repair?.name}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: 120,
    },
    {
      title: "Status",
      width: 100,
      render: (row) => (
        <span className={columnClass}>
          {
            <Text
              type={
                row.repairapprovalstatus == 2 || row.transferstatus == 4
                  ? "success"
                  : row.repairapprovalstatus == 3 || row.transferstatus == 5
                  ? "danger"
                  : "warning"
              }
            >
              {row.repairapprovalstatus_name == null
                ? "Pending"
                : row.repairapprovalstatus_name}
            </Text>
          }
        </span>
      ),
    },
    {
      title: "Reason",
      render: (row) => (
        <span className={columnClass}>{row.rejectdescription}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: 100,
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          {row.repairapprovalstatus == 1 && (
            <>
              {props.userAccessList?.indexOf(
                "StockTransfer.change_repairamount"
              ) !== -1 && (
                <i
                  className="ri-edit-box-line hp-mr-2 "
                  style={{ cursor: "pointer", color: "#fc6f03" }}
                  onClick={() => {
                    props.getPurchaseStock({
                      ...props.purchaseStockParams,
                      page: 1,
                    });
                    props.setPurchaseStockParams({
                      ...props.purchaseStockParams,
                      page: 1,
                    });
                    props.getRepairAmountByID(row.id);
                    props.isModelVisible(true);
                  }}
                ></i>
              )}

              {props.userAccessList?.indexOf(
                "StockTransfer.delete_repairamount"
              ) !== -1 && (
                <Popconfirm
                  placement="topRight"
                  title="Are you sure to delete this Record?"
                  onConfirm={() => props.repairAmountDelete(record.id)}
                  okText="Yes"
                  cancelText="No"
                  className="hp-ml-8"
                  icon={
                    <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                  }
                >
                  <i
                    className="ri-delete-bin-2-line "
                    style={{ cursor: "pointer", color: "#ff0000" }}
                  ></i>
                </Popconfirm>
              )}
            </>
          )}
          {props.userAccessList?.indexOf("StockTransfer.view_repairamount") !==
            -1 && (
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getRepairAmountByID(row.id);
                props.isRepairAmountViewModelVisible(true);
              }}
            ></i>
          )}
        </Space>
      ),
    },
  ];
  const { Search } = Input;

  const data = props.repairAmountList.map((row, key) => ({
    ...row,
    index:
      (props.reapirAmountParams.page - 1) * props.reapirAmountParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.clearReapirAmountList();
    props.SetRepairAmountParams({
      ...props.reapirAmountParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getRepairAmount({
      ...props.reapirAmountParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetRepairAmountParams({ ...props.reapirAmountParams, search: val });
    props.getRepairAmount({ ...props.reapirAmountParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[12, 12]}>
      <PageTitle
        pageTitle="Repair Amount"
        // addModelTile={
        //   props.userAccessList?.indexOf("StockTransfer.add_repairamount") !==
        //     -1 && "Add Repair Amount"
        // }
        // goBack={false}
        // setIsModalVisible={props.isModelVisible}
        // isModalVisible={props.model}
      />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col
            span={24}
            xs={24}
            sm={24}
            md={14}
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search..."
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getRepairAmount({
                    ...props.reapirAmountParams,
                    page: 1,
                    search: "",
                  });
                  props.SetRepairAmountParams({
                    ...props.reapirAmountParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          {props.userAccessList?.indexOf("StockTransfer.add_repairamount") !==
            -1 && (
            <Col span={24} xs={8} sm={8} md={6}>
              <Button
                onClick={() => {
                  props.isModelVisible(true);
                }}
                className="hp-w-100 hp-p-12"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Repair Amount"}
              </Button>
            </Col>
          )}
        </Row>
      </Col>

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.reapirAmountParams?.page}
                pageSize={props.reapirAmountParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddRepairamount />
      <RepairAmountView />
    </Row>
  );
}

function mapAreaToProps({ repairAmount, mini, system }) {
  return {
    repairAmountList: repairAmount.repairAmountList,
    noofpages: repairAmount.noofpages,
    listCount: repairAmount.listCount,
    reapirAmountParams: repairAmount.reapirAmountParams,
    loading: repairAmount.loading,
    userAccessList: system.userAccessList,
    purchaseStockParams: mini.purchaseStockParams,
  };
}

export default connect(mapAreaToProps, {
  getRepairAmount,
  getRepairAmountByID,
  repairAmountDelete,
  isModelVisible,
  SetRepairAmountParams,
  getPurchaseStock,
  setPurchaseStockParams,
  isRepairAmountViewModelVisible,
  clearReapirAmountList,
})(RepairAmount);
